import Typography from '@material-ui/core/Typography';
import React from 'react';
import { LinearProgress } from '@material-ui/core';

export default class AreaExpansionPanelDetail extends React.Component < {area: string} > {
    
    state: {
        ready: false
    }

    render () {
        return this.state.ready ? <p>{this.props.area}</p> : <LinearProgress />
    }
}